* {
  margin: 0;
  padding: 0;
}

.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  height: 50vh;
  width: 50vw;
  align-items: center;
  padding: 1rem;
}
input {
  background-color: rgb(226, 217, 217);
  padding: 10px;
}
input,
button,
select {
  height: 30px;
  border: none;
}
input:focus,
button:focus,
select:focus {
  outline: none;
}

button {
  width: 2rem;
  background: none;
  background-color: rgb(248, 248, 248);
}
.form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin: 40px 0px;
}

i {
  background-color: rgb(252, 89, 89);
  padding: 2px;
  border-radius: 5px;
  filter: invert();
}

select {
  width: 200px;
}

ul {
  text-align: center;
  list-style: none;
  display: grid;
  align-items: center;
  justify-content: center;
}

.todo-item {
  display: flex;
  margin: 15px 0;
  box-shadow: 0px 2px 40px rgb(174, 191, 204);
}
.todo-item > li {
  background-color: white;
  min-width: 25vw;
}

.todo-item > button > i {
  background: none;
}

.trash {
  background-color: red;
}

.check {
  background-color: gray;
}

.completed {
  color: aqua;
  text-decoration: line-through;
}
